/**
 * 转换色值(#ff5acbd3 带 alpha 通道的色值转换成 rgba, 普通色值不变)
 * @param {String} color - 色值
 * @return {String}
 * */
export function parseColor(color) {
  if(!isString(color)) {
    throw new TypeError('[buttercam~parseColor] color must be String')
  }
  if(!/^#/.test(color)) {
    throw new TypeError('[buttercam~parseColor] color is invalid')
  }
  if(color.length <= 7) {
    return color
  }
  if(color.length === 9) {
    try {
      const alpha = (parseInt(color.slice(1, 3), 16) / 255).toFixed(2);
      const r = parseInt(color.slice(3, 5), 16);
      const g = parseInt(color.slice(5, 7), 16);
      const b = parseInt(color.slice(7), 16);
      return `rgba(${r},${g},${b},${alpha})`
    } catch (e) {
      return color
    }
  }
  return color
}

/**
 * 滑动到目标位置
 * @param {Object|String} target - 目标，可以是 css selector 或是 dom object
 * @param {Object} [options] - 选项
 * @param {Number} [options.duration] - 时长
 * @param {Number} [options.offset] - 偏移
 * @param {Function} [options.callback] - 回调，活动结束后触发
 * @param {Function} [options.easing] - 过渡函数
 * @return void
 * */
export function scrollTo(target, options) {
  jump.to(target, options)
}

/**
 * 获取执行器
 * @param {Function} fun - 要被执行的函数
 * @param {Object} context - 执行环境
 * @return {*} 优先返回函数返回值，否则返回 undefined
 * */
export function getExecutor(fun, context) {
  return function (...args) {
    try {
      return fun.apply(context || this, args)
    } catch (e) {
      return undefined
    }
  }
}

/**
 * 通过名称获取执行器
 * @param {string} name - 名称
 * @param {Object} namespace=window - 命名空间
 * @return {*}
 * */
export function getExecutorByName(name, namespace = global) {
  try {
    return getExecutor(namespace[name], namespace)
  } catch (e) {
    return () => undefined
  }
}

/**
 * 发送 scheme
 * @function
 * @param {String} scheme - scheme
 * @return {Promise}
 * */
export async function sendScheme(scheme) {
  const document = global.document;
  const iframe = document.createElement('iframe');
  iframe.style.width = '0';
  iframe.style.height = '0';
  iframe.style.border = '0';
  iframe.style.padding = '0';
  iframe.style.margin = '0';
  iframe.style.opacity = '0';
  iframe.src = scheme;
  document.body.appendChild(iframe);
  return new Promise((resolve) => {
    setTimeout(() => {
      document.body.removeChild(iframe);
      resolve()
    }, 10)
  })
}

/**
 * 暴力克隆(函数无法克隆)
 * @param {Object|Array} object - 克隆对象
 * @returns {Object}
 * */
export function forceClone(object) {
  try {
    return JSON.parse(JSON.stringify(object))
  } catch (e) {
    return object
  }
}

/**
 * 内存中加载图片
 * @param {String} src - 图片地址
 * @param {Object} attrs - img 标签附加属性
 * @returns {Promise}
 * */
export function loadImage(src, attrs = {}) {
  return new Promise(function (resolve, reject) {
    const image = new Image();
    
    image.onload = function () {
      resolve(image)
    };
    
    image.onerror = function (err) {
      reject(new Error({src, message: err.message}))
    };
    
    Object.keys(attrs).forEach((key) => {
      image.setAttribute(key, attrs[key])
    });
    
    image.src = src
  })
}

/**
 * 等待
 * @param {Number} time - 延迟时间
 * @returns {Promise}
 * @example
 * async function() {
 *   await sleep(100); //(异步)等待 100ms
 *   //do something...
 * }
 * */
export function sleep(time) {
  return new Promise(resolve => {
    setTimeout(resolve, time)
  })
}